.paginate-box {
    display: flex;
    justify-content: center;
    padding: 20px 0;
    color: white;
}
.output_box img {
    width: 100%;
    height: 300px;
}

.output_box p{
    color: #a78d9b;
    text-align: center;
    transition: .3s;
}

.output_box p:hover {
    color: #ffa700;
}

.MuiPaginationItem-root {
    color: #fff!important;
}